.fancybox-enabled {
	overflow: auto; }
// FANCYBOX
#__bs_notify__ {
	top: 50% !important;
	left: 50% !important;
	right: unset !important;
	bottom: unset !important;
	@include transform(translateX(-50%)translateY(-50%));
	@include bor-rad(5px); }
.fancybox-bg {}
.fancybox-is-open {
	.fancybox-bg {} }
.fancybox-button--close {}
.fancybox-slide {
	overflow: unset;
	>* {
		overflow: unset; } }
.fancybox-close-small {
	width: unset;
	height: unset;
	top: -15px;
	right: -15px;
	@include media-breakpoint-down(sm) {
		top: 5px;
		right: 5px; }
	&:after {
		background: darken($white,10%);
		color: $main;
		content: '';
		@include fs(18rem);
		width: 36px;
		height: 36px;
		line-height: 36px;
		@include bor-rad(50px);
		display: block;
		@include pos(static,unset);
		@include media-breakpoint-down(sm) {
			@include fs(16rem);
			width: 30px;
			height: 30px;
			line-height: 30px; } }
	&:hover {
		&:after {
			background: lighten($red,45%);
			color: $red; } } }


.popup-sizechart {
	@include transform(scale3d(0.5,0.5,0.5));
	@include transition(all,0.5s);
	width: 800px;
	@include media-breakpoint-down(md) {
		width: 700px; }
	.popup-title {
		@include fs(28rem);
		font-weight: 300;
		text-transform: uppercase;
		margin-bottom: 20px;
		@include media-breakpoint-down(md) {
			@include fs(24rem);
			margin-bottom: 15px; } }
	.des {
		@include fs(14rem);
		color: darken($white,40%);
		margin-bottom: 25px;
		@include media-breakpoint-down(md) {
			@include fs(13rem);
			font-weight: 300;
			margin-bottom: 15px; } }
	.option-group {
		margin-bottom: 30px;
		@include media-breakpoint-down(md) {
			margin-bottom: 20px; }
		.module-title {
			@include fs(13rem);
			font-weight: 700;
			color: $main;
			border-bottom: 1px solid $main;
			margin-bottom: 20px;
			@include media-breakpoint-down(md) {
				margin-bottom: 15px; } }
		.form-group {
			margin-bottom: 15px;
			@include media-breakpoint-down(md) {
				margin-bottom: 10px; }
			label {
				display: inline-block;
				vertical-align: middle;
				font-weight: 300;
				@include fs(13rem);
				margin-right: 15px;
				text-transform: uppercase;
				@include media-breakpoint-down(md) {
					@include fs(12rem);
					margin-right: 10px; } }
			select, input {
				display: inline-block;
				vertical-align: middle;
				width: 40px;
				@include fs(13rem);
				padding: 3px 5px;
				border: 1px solid darken($white,10%);
				@include bor-rad(3px); }
			input[type="radio"] {
				margin-right: 0;
				width: unset; } }
		.size-choice {
			a {
				display: inline-block;
				padding: 3px 5px;
				@include fs(12rem);
				// +bor-rad(3px)
				margin-right: 5px;
				margin-bottom: 5px;
				color: $main;
				border: 1px solid darken($white,10%);
				&:hover {
					color: $main; }
				&.active {
					background: $main;
					color: $white;
					border-color: $main; } } } }
	.btn-choose {
		padding: 15px 0;
		text-align: center;
		color: $white;
		background: $main;
		text-transform: uppercase;
		display: block;
		&:hover {
			background: lighten($main,10%); } } }
.fancybox-slide--current {
	.popup-sizechart {
		@include transform(scale3d(1,1,1)); } }
// END FANCY BOX

// ECOMERCE
.input-number {
	display: inline-block;
	border: 1px solid darken($white,10%);
