.subscribefrm {
	position: relative;
	.fa.fa-exclamation-triangle {
		color: red;
		@include fs(15rem);
		display: block;
		position: absolute;
		top: 10px;
		right: 15px;
		text-indent: -9999px;
		width: 15px;
		height: 15px;
		line-height: 15px;
		text-align: center;
		&:after {
			content: '\f071';
