@keyframes prnew {
	0% {
		background: $orange; }
	50% {
		background: $yellow; }
	100% {
		background: $orange; } }
@keyframes prsale {
	0% {
		background: $dred; }
	50% {
		background: $red; }
	100% {
		background: $dred; } }
.col-left {
	width: 280px;
	padding: 0px 15px;
	// margin-top: 20px
	@include media-breakpoint-down(lg) {
		width: 230px; }
	@include media-breakpoint-down(md) {
		width: 0;
		margin: 0; } }
.col-main {
	width: calc(100% - 280px);
	padding: 0px 15px;
	// border-left: 1px solid darken($white,10%)
	// margin-top: 20px
	@include media-breakpoint-down(lg) {
		width: calc(100% - 230px); }
	@include media-breakpoint-down(md) {
		width: 100%;
		border-left: none; } }

.bannerads {
	margin-bottom: 20px;
	overflow: hidden;
	@include media-breakpoint-down(sm) {
		margin-bottom: 10px; }
	.slick-prev,.slick-next {
		@include pos(abs,50% null null null);
		@include transform(translateY(-50%));
		@include fs(0);
		z-index: 5;
		color: $white;
		opacity: 0;
		&:before {
			content: '';
			@include fs(32rem); } }
	&:hover {
		.slick-prev,.slick-next {
			opacity: 1; } }
	.slick-prev {
		left: 15px;
		&:before {
			content: '\f104'; } }
	.slick-next {
		right: 15px;
		&:before {
			content: '\f105'; } }
	.slick-dots {
		@include pos(abs, null 30px 10px null);
		opacity: 0;
		li {
			display: inline-block;
			width: 10px;
			height: 10px;
			@include bor-rad(50px);
			background: $white;
			cursor: pointer;
			@include transition(all,0.5s);
			&.slick-active {
				background: $red; }
			+ li {
				margin-left: 8px; }
			button {
				display: none; } } } }

.category-info {
	// background: darken($white,5%)
	border-bottom: 1px solid $main;
	// +bor-rad(5px)
	padding: 10px 0px;
	margin-bottom: 30px;
	.cate-name {
		font-weight: 500;
		text-transform: uppercase;
		@include fs(18rem);
		float: left;
		margin-bottom: 5px; }
	.amount {
		float: left;
		clear: left;
		color: darken($white,40%);
		@include fs(12rem); }
	.product-sort {
		float: right;
		padding-top: 4px;
		select {
			border: 1px solid darken($white,10%);
			// +bor-rad(3px)
			padding: 5px 10px;
			color: darken($white,60%); } } }
.product-list {
	&.product-list2 {
		.row {
			margin: 0px -5px; }
		.col-custom {
			padding: 0px 5px;
			@include media-breakpoint-up(xl) {
				@include flex(0 0 20%); } } }
	.product-col {
		background: $white;
		figure {
			padding: 10px;
			@include pos(rel,null);
			.promotion {
				@include pos(abs, 10px null null 10px);
				z-index: 5;
				img {
					width: 25%; }
				&.new {}
				&.sale {}
				&.hot {} }
			.img-wrap {
				@include pos(rel,null);
				.product-img {
					// display: block
					// padding: 5px
					@include flexbox;
					justify-content: center;
					align-items: center;
					padding: 10px 0;
					// height: 200px
					img {
						max-width: 90%;
						max-height: 90%;
						@include transition(all,0.3s); } } }
			figcaption {
				text-align: center;
				padding: 0px 10px;
				@include pos(rel,null);
				.product-brand {
					@include fs(11rem);
					text-transform: uppercase;
					font-weight: 300;
					color: darken($white,40%);
					line-height: 1.5;
					@include media-breakpoint-down(md) {
						@include fs(10rem); } }
				.product-name {
					@include fs(15rem);
					text-transform: uppercase;
					line-height: 1.5;
					@include media-breakpoint-down(md) {
						@include fs(13rem); }
					// font-weight: 500
					a {
						color: $main; } }
				.product-status {
					@include fs(12rem);
					font-weight: 300;
					color: darken($white,40%);
					line-height: 1.5;
					@include media-breakpoint-down(md) {
						@include fs(10rem); } }
				.product-view {
					@include fs(12rem);
					font-weight: 300;
					color: darken($white,40%);
					line-height: 1.5;
					margin-bottom: 10px;
					@include media-breakpoint-down(md) {
						@include fs(10rem); } }
				.product-price {
					border-top: 1px solid color-rgba($black,0.1);
					padding: 5px 0;
					@include flexbox;
					flex-wrap: wrap;
					justify-content: center;
					.new {
						@include fs(13rem);
						font-weight: 500;
						color: $red;
						padding: 0px 3px; }
					.old {
						@include fs(11rem);
						text-decoration: line-through;
						color: darken($white,40%);
						margin-top: 1px;
						padding: 0px 3px; } }
				.btn-productwrap {
					display: none; }
				.btn-view {
					@include pos(abs, 50% null null 50%);
					@include transform(translateX(-50%));
					border: 1px solid $black;
					background: $black;
					color: $white;
					text-transform: uppercase;
					@include fs(14rem);
					padding: 0px 20px;
					line-height: 34px;
					margin-top: -17px;
					white-space: nowrap;
					opacity: 0;
					visibility: 0;
					display: none;
					&:hover {
						background: $red;
						border-color: $red;
						color: $white; } } }
			&:hover {
				.img-wrap {
					.product-img {
						img {
							// opacity: 0.5
							@include transform(scale3d(1.2,1.2,1)); } } }
				figcaption {
					.btn-view {
						opacity: 1; } } } } } }
.product-page {
	.product-list {
		border-top: 1px solid darken($white,10%);
		border-left: 1px solid darken($white,10%);
		.row {
			margin: 0px 0px; }
		.col-6 {
			padding: 0px 0px; }
		.product-col {
			border-right: 1px solid darken($white,10%);
			border-bottom: 1px solid darken($white,10%); } }

	.slidewrap {
		@include pos(rel,null);
		@include flexbox;
		margin-bottom: 30px;
		@include media-breakpoint-down(xs) {
			margin-bottom: 20px; }
		.product-name {
			@include fs(15rem);
			font-weight: 700;
			color: $orange;
			background: lighten($orange,65%);
			padding: 10px;
			margin: 10px 0;
			@include bor-rad(5px);
			width: 100%; }
		.promotion {
			color: $white;
			@include bor-rad(3px);
			@include fs(11rem);
			font-weight: 700;
			text-transform: uppercase;
			line-height: 1;
			padding: 3px 5px;
			margin-bottom: -17px;
			@include pos(rel,null);
			z-index: 5;
			&.new {
				background: $orange; }
			&.sale {
				background: $dred;
				animation-name: prsale;
				animation-duration: 1s;
				animation-iteration-count: infinite; } }
		.product-slide {
			width: calc(100% - 80px);
			margin-left: 80px;
			@include media-breakpoint-down(md) {
				width: calc(100% - 64px);
				margin-left: 64px; }
			@include media-breakpoint-down(xs) {
				width: 100%;
				margin-left: 0; }
			.item {
				.fancybox {
					@include flexbox;
					width: 100%;
					height: 400px;
					justify-content: center;
					align-items: center;
					@include media-breakpoint-down(md) {
						height: 350px; }
					@include media-breakpoint-down(xs) {
						height: 300px; }
					img {
						max-width: 100%;
						max-height: 100%;
						@include media-breakpoint-down(md) {
							max-width: 100%;
							max-height: 100%; }
						@include media-breakpoint-down(xs) {
							max-width: 80%;
							max-height: 80%; } } } } }
		.product-nav {
			@include pos(abs, 0 null null 0);
			width: 64px;
			@include media-breakpoint-down(md) {
				width: 64px; }
			@include media-breakpoint-down(xs) {
				@include pos(rel,null);
				width: 100%;
				padding: 0px 20px; }
			.slick-prev, .slick-next {
				@include fs(0);
				display: block;
				width: 100%;
				text-align: center;
				line-height: 1;
				color: darken($white,40%);
				@include media-breakpoint-down(xs) {
					@include pos(abs, 50% null null null);
					@include transform(translateY(-50%));
					z-index: 5;
					width: unset; }
				&:hover {
					color: $main; }
				&:before {
					content: '';
					@include fs(28rem); } }
			.slick-prev {
				@include media-breakpoint-down(xs) {
					left: 0; }
				&:before {
					content: '\f106';
					@include media-breakpoint-down(xs) {
						content: '\f104'; } } }
			.slick-next {
				@include media-breakpoint-down(xs) {
					right: 0; }
				&:before {
					content: '\f107';
					@include media-breakpoint-down(xs) {
						content: '\f105'; } } }
			.item {
				padding: 5px 0;
				@include media-breakpoint-down(xs) {
					padding: 0; }
				.img {
					width: 60px;
					height: 60px;
					@include flexbox;
					justify-content: center;
					align-items: center;
					border: 2px solid transparent;
					cursor: pointer;
					overflow: hidden;
					@include transition(all,0.5s);
					@include media-breakpoint-down(md) {
						width: 60px;
						height: 60px; }
					@include media-breakpoint-down(xs) {
						width: 100%;
						height: 60px; }
					img {
						max-width: 80%;
						width: 80%;
						@include media-breakpoint-down(xs) {
							max-width: 70%; } } }
				&.slick-current {
					.img {
						border-color: darken($white,20%); } } } } }
