					display: block;
					color: darken($white,40%);
					line-height: 40px;
					padding: 0px 10px;
					@include fs(14rem);
					font-weight: 500;
					// font-weight: 500
					// text-transform: uppercase
					@include pos(rel,null);
					&:before {
						content: '';
						display: block;
						@include pos(abs, null 100% 0 0);
						height: 2px;
						background: $black;
						@include transition(all,0.3s); } }
				&:hover {
					a {
						color: $main;
						&:before {
							right: 0; } } }
				&.active, &.r-tabs-state-active {
					a {
						font-weight: 600;
						color: $black;
						&:before {
							right: 0; } } } } }
		.r-tabs-accordion-title {
			margin-top: 10px;
			a {
				display: block;
				background: darken($white,5%);
				color: $main;
				@include fs(14rem);
				font-weight: 500;
				padding: 10px 20px;
				@include pos(rel,null);
				&:after {
					content: '\f107';
					display: block;
					@include pos(abs, 10px 20px null null);
					@include transition(all,0.3s); } }
			&.r-tabs-state-active {
				a {
					background: darken($white,15%);
					color: $red;
					&:after {
						@include transform(scaleY(-1)); } } } }
		.tab-content {
