				margin-bottom: 20px; }
			.feature-col {
				width: 20%;
				height: 200px;
				margin-bottom: 40px;
				@include pos(rel,null);
				overflow: hidden;
				text-align: center;
				cursor: pointer;
				@include media-breakpoint-down(lg) {
					width: 25%; }
				@include media-breakpoint-down(md) {
					width: 33.33%; }
				@include media-breakpoint-down(sm) {
					width: 50%;
					height: auto;
					overflow: visible; }
				@include media-breakpoint-down(xs) {
					width: 100%;
					margin-bottom: 20px; }
				figure {
					.feature-icon {
						@include flexbox;
						justify-content: center;
						align-items: center;
						height: 70px;
						margin-bottom: 15px; }
					figcaption {
						.feature-name {
							font-weight: 700;
							color: $main; }
						.feature-des {
							@include fs(12rem);
							padding: 10px 15px;
							@include pos(abs, 140px 0 null 0);
							z-index: 5;
							color: darken($white,40%);
							border: 1px solid transparent;
							background: $white;
							@include media-breakpoint-down(sm) {
								@include pos(static,null);
								max-width: 80%;
								margin: 10px auto; }
							@include media-breakpoint-down(xs) {
								border-color: darken($white,40%); } } } }
				&:hover {
					overflow: visible;
					figure {
						figcaption {
							.feature-des {
								border: 1px solid darken($white,40%); } } } } } }
