		figure {
			text-align: center;
			.contain-img {}
			figcaption {
				padding-top: 10px;
				.number {
					@include fs(24rem);
					color: $orange;
					font-weight: 600;
 }					// margin-bottom: 10px
				.name {
					@include fs(18rem); } } } } }

.small-title {
	@include fs(14rem);
	margin-bottom: 10px;
	color: $white;
	background: $black;
	padding: 10px 20px;
	text-transform: uppercase;
	// &:after
	// 	content: ''
	// 	display: block
	// 	width: 60px
	// 	height: 1px
	// 	background: $red
 }	/// margin-top: 5px
.product-service {
	margin-bottom: 30px;
	.content {
		background: darken($white,3%);
		padding: 15px;
		@include fs(14rem); } }
.product-other {
	margin-bottom: 30px;
	.product-list {
		border-top: 1px solid darken($white,10%);
		border-left: 1px solid darken($white,10%);
		.row {
			margin: 0px 0px; }
		.col-6 {
			padding: 0px 0px; }
		.product-col {
			border-right: 1px solid darken($white,10%);
			border-bottom: 1px solid darken($white,10%); } }
	.other-slide {
		@include pos(rel,null);
		&:after {
			content: '';
			@include pos(abs, 0 0 0 null);
			width: 1px;
			background: darken($white,10%); }
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			text-align: center;
			@include bor-rad(50px);
			z-index: 10;
			color: darken($white,60%);
			opacity: 0;
			&:hover {
				color: $orange; }
			&.slick-disabled {
				opacity: 0; }
			&:before {
				content: '';
				font-family: 'fontawesome';
				@include fs(28rem); } }
		&:hover {
			.slick-prev, .slick-next {
				opacity: 1; } }
		.slick-prev {
			left: 10px;
			&:before {
				content: '\f104'; } }
		.slick-next {
			right: 10px;
			&:before {
				content: '\f105'; } } } }

