.page-title {
	@include fs(20rem);
	text-transform: uppercase;
	font-weight: 500;
	margin-bottom: 20px;
	line-height: 1.5;
	// border-left: 4px solid $red
	// padding-left: 10px
	&:after {
		content: '';
		display: block;
		width: 120px;
		height: 1px;
		background: $red;
		margin-top: 5px; }
	@include media-breakpoint-down(md) {
		@include fs(18rem); }
	@include media-breakpoint-down(xs) {
		@include fs(16rem); } }
