.lastest-news {
	margin: 40px 0;
	.post-list {
		padding: 10px;
		border: 1px solid darken($white,10%); }
	.post {
		padding: 7px;
		border-bottom: 1px dashed darken($white,10%);
		&.big {
			padding: 0;
			figure {
				@include pos(rel,null);
				.post-img {
					filter: brightness(70%); }
				figcaption {
					@include pos(abs, null 15px 15px 15px);
					z-index: 5;
					.post-name {
						@include fs(15rem);
						a {
							color: $white; } }
					time {
						color: $white;
						opacity: 0.7; } } } }
		figure {
			@include flexbox;
			.post-img {
				display: block; }
			figcaption {
				.post-name {
					@include fs(13rem);
					line-height: 1.3;
					font-weight: 500;
					a {
						color: $black;
						&:hover {
							color: $red; } } }
				time {
					@include fs(11rem);
					color: darken($white,40%);
					&:before {
						content: '\f017';
						margin-right: 5px; } } } } } }
.news-page {
	margin: 30px 0;
	.news-list {
		.col-12 {
			margin-bottom: 20px; }
		.news-col {
			figure {
				.news-img {
					display: block; }
				figcaption {
					padding: 10px 0;
					.news-name {
						@include fs(16rem);
						line-height: 1.5;
						font-weight: 500;
						a {
							color: $black; } }
					time {
						@include fs(12rem);
						color: darken($white,40%);
						display: block;
						margin-bottom: 10px;
						&:before {
							content: '\f017';
							margin-right: 5px; } }
					.news-des {
						@include fs(14rem);
						color: darken($white,60%); } }
				&:hover {
					figcaption {
						.news-name {
							a {
								color: $red; } } } } } } }
	.btn-wrap {
		@include flexbox;
		justify-content: center;
		.btn-viewmore {
			display: block;
			line-height: 36px;
			border: 1px solid $black;
			text-transform: uppercase;
			@include fs(13rem);
			padding: 0px 30px;
			color: $black;
			&:hover {
				color: $white;
				background: $black; } } }
	.content {
		+ .share {
			margin-top: 20px; } }
	.share {
		margin-bottom: 30px; } }
.news-info {
	margin-top: -10px;
	margin-bottom: 20px;
	time {
		@include fs(13rem);
		color: darken($white,40%);
		&:before {
			content: '\f133';
			margin-right: 5px; } } }
.other-news {
	.post-list {
		border-top: 1px dashed darken($white,10%);
		.post {
			display: block;
			padding: 10px 0;
			@include fs(13rem);
			color: $main;
			border-bottom: 1px dashed darken($white,10%);
			&:hover {
				color: $red; } } } }
