.checkbox input {
  display: none;
}

.checkbox input + label {
  color: #333;
  padding-left: 25px;
  position: relative;
}

.checkbox input + label:hover {
  color: #000;
}

.checkbox input + label:before {
  content: '';
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 3px;
  border: 1px solid #cccccc;
  position: absolute;
  top: 2px;
  left: 0;
}

.checkbox input + label:after {
  content: '\f00c';
  font-family: 'fontawesome';
  display: block;
  width: 16px;
  height: 16px;
  line-height: 14px;
  text-align: center;
  border-radius: 3px;
  color: #ff0000;
  border: 1px solid #cccccc;
  position: absolute;
  top: 2px;
  left: 0;
  z-index: 1;
  -webkit-transition: all, 0.5s;
  -khtml-transition: all, 0.5s;
  -moz-transition: all, 0.5s;
  -ms-transition: all, 0.5s;
  -o-transition: all, 0.5s;
  transition: all, 0.5s;
  opacity: 0;
}

.checkbox input:checked + label:after {
  opacity: 1;
}

