  -khtml-transition: all, 0.3s;
  -moz-transition: all, 0.3s;
  -ms-transition: all, 0.3s;
  -o-transition: all, 0.3s;
  transition: all, 0.3s;
}

.radio input[type="radio"] + label:hover {
  color: #333;
}

.radio input[type="radio"] + label:before {
  content: '';
  display: block;
  width: 14px;
  height: 14px;
  border-radius: 50px;
  position: absolute;
  top: 8px;
  left: 0;
  border: 1px solid #999999;
