// CART PAGE

.carttitle {
	@include fs(15rem);
	color: $main;
	font-weight: 500;
	line-height: 40px;
	@include pos(rel,null);
	text-transform: uppercase;
	padding-left: 30px;
	// border-bottom: 1px solid $red
	@include media-breakpoint-down(xs) {
		@include fs(14rem);
		padding: 0px 15px;
		padding-left: 40px;
		background: darken($white,5%);
		color: $main;
		line-height: 36px;
		margin: 0; }
	strong {
		color: $main;
		@include media-breakpoint-down(xs) {
			color: $red; } }
	&:before {
		// color: $red
		@include fs(18rem);
		width: 30px;
		line-height: 40px;
		margin-right: 5px;
		text-align: left;
		@include pos(abs, 0 null null 0);
		@include media-breakpoint-down(xs) {
			left: 15px;
			color: $main;
			line-height: 36px; } } }
.cart-page {
	background: $white;
	margin-bottom: 30px;
	// border: 1px solid darken($white,10%)
	// +bor-rad(5px)
	overflow: hidden;
	@include media-breakpoint-down(md) {
		margin-bottom: 20px; }
	@include media-breakpoint-down(xs) {
		padding: 0;
		margin-bottom: 0; }
	.alert {
		margin-top: 10px; }
	label.error {
		@include pos(abs, null 10px 10px null);
		@include fs(0);
		line-height: 1;
		&:before {
			content: '\f071';
			font-family: 'fontawesome';
			@include fs(16rem);
			color: $red; } }
	.carttitle {
		@include media-breakpoint-down(xs);
		&:before {
			content: '\f07a';
			font-family: 'fontawesome';
			margin-right: 5px;
			font-weight: normal; } }
	.cartnotice {
		margin: 0 0 10px;
		@include bor-rad(5px);
		border: 1px solid lighten($red,30%);
		background: lighten($red,40%);
		color: $main;
		padding: 10px 30px 10px 15px;
		@include pos(rel,null);
		@include fs(14rem);
		a {
			color: $red;
			&:hover {
				color: $red; } } }
	.removenotice {
		@include pos(abs, 7px 15px null null);
		cursor: pointer;
		@include fs(16rem);
		color: $red;
		&:before {
			content: '\f057';
			font-family: 'fontawesome'; }
		&:hover {
			color: lighten($red, 20%); } }
	.cart-table {
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		width: 100%;
		@include media-breakpoint-down(lg) {
			margin-bottom: 15px; }
		th, td {
			background: none;
			@include media-breakpoint-up(lg) {
				padding: 10px; } }
		th {
			display: none;
			@include media-breakpoint-up(lg) {
				display: table-cell;
				vertical-align: middle;
				font-weight: 500;
				@include fs(13rem);
				text-transform: uppercase;
				background: darken($white,5%);
				border-bottom: 1px solid darken($white,10%);
				&:last-child {
					text-align: right; } } }
		tbody {
			tr {
				@include media-breakpoint-down(md) {
					@include flexbox;
					flex-wrap: wrap;
					border-bottom: 1px solid darken($white,10%);
					&:last-child {
						border-bottom: none; } } }
			td {}
			.product {
				width: 100%;
				padding: 10px;
				@include pos(rel,null);
				@include media-breakpoint-up(lg) {
					width: calc(100% - 200px - 100px - 200px); }
				figure {
					@include flexbox;
					.product-img {
						display: block;
						width: 80px; }
					figcaption {
						width: calc(100% - 80px);
						padding-left: 10px;
						.product-name {
							@include fs(14rem);
							font-weight: 500;
							line-height: 1.5;
							margin-bottom: 5px;
							a {
								color: $black; } }
						.product-des {
							@include fs(12rem);
							color: darken($white,40%); }
						.product-option {
							@include media-breakpoint-up(lg) {
								@include flexbox;
								flex-wrap: wrap; }
							.label {
								@include fs(13rem);
								display: inline-block;
								vertical-align: middle; }
							.product-size {
								// width: 50%
								margin-bottom: 5px;
								@include media-breakpoint-up(lg) {
									margin: 0; }
								select {
									display: inline-block;
									vertical-align: middle;
									@include bor-rad(3px);
									border: 1px solid darken($white,30%);
									color: $black;
 } }									// font-weight: 600
							.product-color {
								@include media-breakpoint-up(lg) {
									margin-left: 15px;
									@include flexbox;
									align-items: center;
									align-content: center; }
								.color-list {
									display: inline-block;
									vertical-align: top;
									@include media-breakpoint-up(lg) {
										@include flexbox;
										align-content: center;
										align-items: center; } }
								a {
									display: inline-block;
									border: 1px solid darken($white,10%);
									padding: 2px;
									&.active {
										border-color: $red; }
									+ a {
										margin-left: 3px; } }
								.color-img {
									width: 14px;
									height: 14px; }
 } }								// width: 50%
						.removeproduct {
							@include pos(abs, 8px 10px null null);
							color: $lred;
							@include fs(0);
							@include media-breakpoint-up(lg) {
								@include fs(13rem);
								@include pos(static,unset);
								margin-top: 10px;
								display: block;
								color: darken($white,40%); }
							&:hover {
								color: $red;
								.material-icons, .fa {
									opacity: 1; } }
							.material-icons, .fa {
								@include fs(16rem);
								line-height: 1;
								color: $red;
								opacity: 0.5; } } } } }
			.price {
				margin-left: 100px;
				width: calc(100% - 200px);
				@include media-breakpoint-up(lg) {
					width: 150px;
					margin-left: 0; }
				.new {
					display: block;
					@include fs(15rem);
					font-weight: 500;
					color: $red;
					line-height: 1;
					@include media-breakpoint-up(lg) {
						color: $black;
						font-weight: 500; } }
				.old {
					display: inline-block;
					@include fs(11rem);
					text-decoration: line-through;
					color: darken($white,40%);
					font-weight: 500;
					line-height: 1;
					@include media-breakpoint-up(lg) {
						font-weight: 400;
						color: darken($white,20%); } }
				.sale {
					display: inline-block;
					@include fs(12rem);
					line-height: 1;
					color: $lred; } }
			.amount {
				margin-bottom: 10px;
				@include media-breakpoint-up(lg) {
					margin-bottom: 0;
					width: 100px; }
				.input-number {
					width: 90px;
					background: darken($white,5%);
					border: none;
					overflow: hidden;
					.btn-spin {
						width: 30px;
						height: 30px;
						line-height: 30px;
						background: darken($white,10%); }
					input {
						line-height: 30px;
						width: 30px; } } }
			.total {
				display: none;
				@include fs(15rem);
				font-weight: 600;
				color: $red;
				line-height: 1;
				@include media-breakpoint-up(lg) {
					display: table-cell;
					width: 150px;
					text-align: right; } } } }
	.totalamount {
		text-align: right;
		// padding: 0 20px
		@include fs(14rem);
		strong {
			color: $red;
			@include fs(24rem); }
		@include media-breakpoint-down(sm) {
			margin-top: 10px;
			@include fs(13rem);
			strong {
				@include fs(18rem); } } }
	.payment-des {
		text-align: right;
		font-style: italic;
		// padding: 0 20px
		margin-bottom: 15px;
		@include fs(13rem);
		color: darken($white,40%);
		@include media-breakpoint-down(xs) {
			@include fs(12rem);
			color: darken($white,40%);
			padding: 0px 15px;
			line-height: 1;
			margin-bottom: 10px; } } }
.product-lastview {
	margin-bottom: 40px;
	.carttitle {
		&:before {
			content: '\f017'; } }
	.cart-lastview-slide {
		border: 1px solid darken($white,10%);
		border-left: none;
		// +bor-rad(5px)
		// padding: 10px
		&:before {
			content: '';
			@include pos(abs, 0 null 0 0);
			width: 1px;
			background: darken($white,10%);
			z-index: 5; }
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include fs(0);
			width: 36px;
			height: 36px;
			line-height: 36px;
			text-align: center;
			@include bor-rad(50px);
			background: darken($white,10%);
			z-index: 5;
			&.slick-disabled {
				opacity: 0; }
			&:before {
				content: '';
				font-family: 'fontawesome';
				@include fs(18rem); } }
		.slick-prev {
			left: 0;
			&:before {
				content: '\f104'; } }
		.slick-next {
			right: 0;
			&:before {
				content: '\f105'; } }
		.item {
			border-left: 1px solid darken($white,10%);
			overflow: hidden; } } }
.cart-page {
	.btn-wrap {
		// margin-bottom: 30px
		@include media-breakpoint-down(md) {
			margin-bottom: 20px; }
		@include media-breakpoint-down(xs) {
			// padding: 0px 15px 10px
			// +flexbox
			// text-align: center
			// +pos(fix, null 0 0 0)
			// z-index: 50
			// border-top: 1px solid darken($white,10%)
 } } }			// box-shadow: 0 0 30px color-rgba($black,0.1)
.btn-back {
	display: block;
	background: darken($white,10%);
	color: $main;
	// +fs(14rem)
	// +bor-rad(5px)
	line-height: 40px;
	padding: 0px 20px;
	float: left;
	text-align: center;
	@include media-breakpoint-down(xs) {
		background: none;
		width: 100%;
		margin-bottom: 0;
		color: darken($white,60%);
		float: none; }
	&:hover {
		background: $gray;
		color: $white; }
	&:before {
		content: '\f104';
		font-family: 'fontawesome';
		margin-right: 15px;
		@include media-breakpoint-down(sm) {
			display: none; } } }
.btn-continue {
	display: block;
	background: $red;
	overflow: hidden;
	color: $white;
	line-height: 40px;
	padding: 0px 20px;
	float: right;
	margin: 0;
	// +bor-rad(5px)
	text-align: center;
	@include media-breakpoint-down(xs) {
		width: 100%;
		margin: 0;
		text-transform: uppercase;
		@include fs(15rem);
		float: none; }
	&:hover {
		color: $white; }
	&:after {
		content: '\f105';
		font-family: 'fontawesome';
		margin-left: 15px;
		@include media-breakpoint-down(sm) {
			display: none; } }
	&:before {
		background: color-rgba($white,0.2); } }

.cartpage {
	.nopadding {
		background: $white;
		+ .nopadding {
			border-left: 1px solid darken($white,10%); } } }

.select-group {
	margin-bottom: 10px;
	input[type="radio"] {
		display: none;
		+ label {
			padding: 5px 0 5px 20px;
			@include pos(rel,null);
			color: darken($white,60%);
			@include transition(all,0.3s);
			@include fs(14rem);
			@include media-breakpoint-down(xs) {
				padding: 0px 0 0 20px; }
			&:hover {
				color: $black; }
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				@include pos(abs, 7px null null 0);
				border: 1px solid darken($white,40%);
				@include media-breakpoint-down(xs) {
					top: 3px; } }
			&:after {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				border: 4px solid $red;
				@include pos(abs, 7px null null 0);
				@include bor-rad(50px);
				opacity: 0;
				@include transition(all,0.3s);
				@include media-breakpoint-down(xs) {
					top: 3px; } } }
		&:checked {
			+ label {
				color: $main;
				&:after {
					opacity: 1; } } } } }

.check-group {
	margin-bottom: 10px;
	input {
		display: none;
		+ label {
			color: $main;
			padding-left: 25px;
			@include pos(rel,null);
			&:hover {
				color: $black; }
			&:before {
				content: '';
				display: block;
				width: 16px;
				height: 16px;
				// +bor-rad(3px)
				border: 1px solid darken($white,20%);
				@include pos(abs, 1px null null 0); }
			&:after {
				content: '\f00c';
				font-family: 'fontawesome';
				display: block;
				width: 16px;
				height: 16px;
				line-height: 14px;
				text-align: center;
				// +bor-rad(3px)
				color: $red;
				border: 1px solid darken($white,20%);
				@include pos(abs, 1px null null 0);
				z-index: 1;
				@include transition(all,0.5s);
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }

// CART LOGIN
.cart-login {
	// border: 1px solid darken($white,10%)
	// +bor-rad(5px)
	margin-bottom: 20px;
	background: $white;
	// padding: 20px 0
	overflow: hidden;
	@include media-breakpoint-down(md);
	@include media-breakpoint-down(xs) {
		padding: 10px 15px;
		@include fs(13rem); }
	.login-form {
		width: 400px;
		margin: 0 auto;
		@include media-breakpoint-down(xs) {
			width: 100%; }
		.module-title {
			@include fs(14rem);
			font-weight: 500;
			margin-bottom: 10px;
			@include media-breakpoint-down(xs) {
				margin-bottom: 10px; } }
		.form-group {
			margin-bottom: 10px;
			@include media-breakpoint-down(xs) {
				margin-bottom: 10px; }
			.select-group {
				@include media-breakpoint-down(xs) {
					margin-bottom: 10px; } }
			input {
				width: 100%;
				border: 1px solid darken($white,20%);
				padding: 0px 15px;
				line-height: 36px;
				@include fs(14rem);
				@include transition(all,0.3s);
				@include bor-rad(0px);
				@include media-breakpoint-down(xs) {
					line-height: 30px;
					@include fs(13rem); }
				&:hover, &:active, &:focus {
					border-color: darken($white,40%); } } }

		.forget-password {
			display: block;
			@include fs(14rem);
			color: $main;
			margin-bottom: 0px;
			@include media-breakpoint-down(xs) {
				@include fs(13rem);
				margin-bottom: 10px; }
			&:hover {
				color: $red; } }
		.btn-continue {
			display: block;
			float: none;
			text-align: center;
			margin: 20px 0 0;
			@include media-breakpoint-down(xs) {
				margin-top: 10px; } }
		.social-login {
			margin: 20px 0 0;
			padding: 0;
			background: none;
			.module-title {
				font-weight: 700;
				margin-bottom: 10px; }
			a {
				display: block;
				padding: 10px 10px;
				color: $white;
				margin-bottom: 10px;
				font-weight: 500;
				text-transform: uppercase;
				text-align: center;
				@include pos(rel,null);
				@include fs(13rem);
				&:hover {
					filter: brightness(120%); }
				.fa {
					width: 40px;
					height: 30px;
					line-height: 30px;
					@include fs(18rem);
					@include pos(abs, 4px null null 1px);
					text-align: center;
					margin-right: 10px;
					border-right: 1px solid color-rgba($white,0.5); } }
			.facebook {
				background: $facebook; }
			.google {
				background: $google; } } } }

