.help {
	display: inline-block;
	margin-left: 10px;
	color: darken($white,40%);
	@include fs(18rem);
	@include pos(rel,null);
	@include media-breakpoint-down(xs) {
		@include fs(16rem); }
	.help-tip {
		@include pos(abs, 50% 0 null null);
		min-width: 140px;
		@include fs(12rem);
		padding: 5px 10px;
		background: $grey;
		@include transform(translateX(calc(100% + 10px))translateY(-50%));
		opacity: 0;
		@include transition(all,0.3s);
		margin-top: 10px;
		cursor: pointer;
		&:before {
			content: '';
			display: block;
			width: 0;
			height: 0;
			border: 7px solid;
			border-color: transparent $grey transparent transparent;
			@include pos(abs, 50% null null 1px);
			@include transform(translateY(-50%)translate(-100%));
			z-index: 10; } }
	&:hover {
		color: $main;
		.help-tip {
			opacity: 1;
			margin-top: 0;
			color: $main;
			&:hover {
				color: $main; } } } }

.receive-address, .export-bill, .coupon, .shipping-kind, .payment-info {
