				@include transition(all,0.3s);
				@include bor-rad(0);
				text-align: center;
				white-space: nowrap;
				@include media-breakpoint-down(xs) {
					padding: 0 15px;
					line-height: 27px;
					@include fs(13rem); }
				&:hover {
					background: darken($red,10%); } } } } }


